@import '../../assets/scss/base';

.hero_banner {
    position: $relative;
    isolation: isolate;
    padding: 20px 0;
    z-index: 1;
    min-height: 640px;
    @media (min-width:1600px) {
        min-height: 800px;
    }
    @media (max-width:1200px) {
        min-height: 540px;
    }
    @at-root {
        .hero_bg {
            pointer-events: $none;
            position: $absolute;
            left: 0;
            right: 0;
            top: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            img {
                height: 100%;
                object-fit: cover;
                object-position: bottom;
            }
        }
        .banner_info {
            min-height: 480px;
            @media (min-width:1600px) {
                min-height: 660px;
            }
            @media (max-width:1200px) {
                min-height: 400px;
            }
            figure {
                width: 100%;
            }
            .banner_content {
                color: $white-color;
                @media (min-width:1200px) {
                    max-width: 80%;
                }
            }
        }
    }
}