@import '../../assets/scss/base';

.getintouch_sec {
    background: $grey-color-500;
    @media (min-width:1441px) {
        padding-right: 5%;
    }
    @media (max-width:767px) {
        .container {
            padding: 0;
        }
    }
    @at-root {
        .gmap {
            width: 100%;
            @media (min-width:1200px) {
                height: 100%;
                position: $absolute;
                top: 0;
                left: 0;
            }
            iframe,
            img {
                width: 100%;
                min-height: 300px;
                object-fit: $cover;
                @media (min-width:1200px) {
                    position: $absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                }
            }
        }
    }
}

.title_h2 {
    p {
        color: rgba($base-color, .7);
    }
}