@import '../../assets/scss/base';

.dropDown {
    position: $absolute;
    top: calc(100% + 10px);
    right: 0;
    min-width: 250px;
    @include transition (all, 0.5s, ease-in-out);
    background: $white-color;
    border: 1px solid #e8e8e8;
    box-shadow: 0 0 10px rgb(0, 0, 0, .10);
    @include border-radius (10px);
    z-index: 1;
    .user-body {
        max-height: 280px;
        overflow-x: $hidden;
        overflow-y: $auto;
    }
    &-list {
        border-bottom: 1px solid $grey-color-300;
        padding: 15px;
        @include flexbox;
        @include align-items ($center);
        text-transform: $capitalize;
        cursor: $pointer;
        &:last-child {
            border-bottom: 0;
        }
        em {
            height: 40px;
            width: 40px;
            padding: 4px;
            margin-right: 10px;
            background: $grey-color-700;
            @include border-radius (50%);
            text-align: $center;
            @include flexbox;
            @include align-items ($center);
            @include justify-content ($center);
            img {
                min-width: 20px;
                max-width: 23px;
                max-height: 20px;
            }
        }
        span {
            font-weight: $font-semibold;
            text-transform: $capitalize;
            margin: $auto 0;
            @include font-size (1);
            color: $base-color;
        }
        &:hover {
            span {
                color: $secondary-color;
            }
        }
    }
}