@import '../../assets/scss/base';

.nav-tabs {
    overflow-x: $auto;
    white-space: nowrap;
    overflow-y: $hidden;
    li {
        border: 0;
        cursor: $pointer;
        @include font-size (1);
        padding: 15px 0;
        margin: 0 30px 0 0;
        color: rgba($base-color, .5);
        position: $relative;
        @media (min-width:1200px) {
            margin: 0 40px 0 0;
        }
        @media (min-width:1600px) {
            margin: 0 60px 0 0;
        }
        &::before {
            content: '';
            width: 0;
            @include opacity (0);
            visibility: $hidden;
            height: 4px;
            position: $absolute;
            bottom: -1px;
            left: 0;
            background: $base-color;
            @include border-radius (6px);
            @include transition (all, .5s, ease-in-out);
        }
        &:hover,
        &.active {
            color: $base-color;
            &::before {
                width: 100%;
                @include opacity(1);
                visibility: $visible;
            }
        }
        &.active {
            font-weight: $font-bold;
        }
    }
}