@import '../../assets/scss/base';

.newsletter_sec {
    background: url(../../assets/images/newsletter_bg.webp) $no-repeat;
    background-position: $center;
    background-size: $cover;
    padding: 60px 0;
    isolation: isolate;
    position: $relative;
    &::before {
        content: '';
        background: rgba($base-color, .7);
        position: $absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }
    .title_h2 {
        p {
            color: rgba($white-color, .5);
        }
    }
}

.newsletter_form {
    margin: 0 $auto;
    max-width: 800px;
    @media (max-width:575px) {
        @include flex-direction (column);
    }
    .mb-4 {
        width: 100%;
        margin-top: 10px;
        margin-bottom: 0 !important;
        @media (min-width:576px) {
            margin-right: 15px;
        }
    }
    .position-relative {
        width: 100%;
    }
    .btn {
        @media (max-width:575px) {
            margin: 7px;
        }
        @media (min-width:576px) {
            margin-left: 10px;
        }
    }
}