@import '../../assets/scss/base';

.user_register {
    padding: 40px;
    margin: 0 $auto;
    max-width: 700px;
    @include border-radius(20px);
    background: $dark-green-color-900;
    .user_block {
        @include border-radius (20px);
        background: $base-color;
        text-align: $center;
        padding: 25px;
        em {
            img {
                max-width: 95px;
                min-height: 70px;
                max-height: 70px;
            }
        }
    }
}