@import '../../assets/scss/base';

.user_profile {
    img {
        width: 100%;
        max-height: 220px;
        object-fit: $cover;
        @include border-radius (10px);
    }
}
.card {
    @include border-radius (10px);
    &-body {
        background-color: $grey-color-500;
        @include border-radius ($inherit);
        padding: 25px;
        label {
            &.text-small {
                color: rgba($base-color, .6);
                @include font-size (.85);
            }
        }
        span {
            &.d-block {
                font-weight: $font-medium;
                em {
                    &.verified {
                        svg {
                            path {
                                fill: $secondary-color;
                            }
                        }
                    }
                    svg {
                        width: 20px;
                        path {
                            width: 20px;
                        }
                    }
                }
            }
        }
    }
}

// ProgressBar

.progress-container {
    box-shadow: 0 4px 5px rgb(0, 0, 0, 0.1);
    position: $absolute;
}

.progress-container,
.progress {
    background-color: #eee;
    border-radius: 5px;
    position: relative;
    height: 7px;
    width: 300px;
}

.progress {
    background-color: $primary-color;
    width: 0;
    transition: width 0.4s linear;
}

.percentage {
    background-color: $primary-color;
    border-radius: 5px;
    box-shadow: 0 4px 5px rgb(0, 0, 0, 0.2);
    color: #fff;
    font-size: 12px;
    padding: 4px;
    position: absolute;
    top: 20px;
    left: 0;
    transform: translateX(-50%);
    width: 40px;
    text-align: center;
    transition: left 0.4s linear;
}

.percentage::after {
    background-color: $primary-color;
    content: '';
    position: $absolute;
    top: -5px;
    left: 50%;
    transform: translateX(-50%) rotate(45deg);
    height: 10px;
    width: 10px;
    z-index: -1;
}