@import '../../assets/scss/base';

.tool_tip {
    position: $relative;
    display: $inline-block;
  .tooltiptext {
    visibility: $hidden;
    white-space: normal;
    word-break: break-word;
    background-color: $base-color;
    @include border-radius (6px);
    min-width: 140px;
    max-width: 400px;
    color: $white-color;
    text-align: $center;
    padding: 5px 5px 5px;
    max-height: 120px;    
    overflow-y: $auto;
    font-size: 12px;
    position: $absolute;
    left: -50%;
    transform: translateX(calc(50% - 115px));
    bottom: 100%;
    z-index: 11;
  }  
  &:hover .tooltiptext {
    visibility: $visible;
  }
}