@import '../../assets/scss/base';

.main {
    padding: 70px 0 0;
    @media (max-width:1200px) {
        padding: 60px 0 0;
    }
}

// About Section
.about_sec {
    @media (min-width:1441px) {
        padding: 50px 0;
    }
    @media (max-width:1440px) {
        padding: 30px 0;
    }
    .container {
        @media (min-width:1440px) {
            max-width: 1250px;
        }
    }
}
.about_summary {
    p {
        color: rgba($base-color, .7);
    }
}

.user_login_sec {
    margin-top: -10%;
    position: $relative;
    z-index: 11;
}