@import '../../assets/scss/base';

::root {
    --bs-modal-bg: $white-color;
    --bs-modal-bg: $grey-color-700;
}

.ReactModal {
    overflow-x: $hidden;
    &__Overlay {
        backdrop-filter: blur(6px);
        background: rgba($black-color, 0.7);
        width: 100%;
        height: 100%;
        position: $fixed;
        top: 0;
        left: 0;
        z-index: 111;
    }
    &__Content {
        padding: 0 15px;
        overflow-x: $hidden;
        overflow-y: $auto;
        position: $fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        outline: 0;
        z-index: 1050;
    }
    &__Body--open {
        overflow: $hidden;
    }
}

.Modal {
    .modal-dialog {
        @media (min-width: 576px) {
            max-width: 530px;
            margin: 1.75rem $auto;
        }
    }
    .btn-close {
        box-shadow: $none !important;
        background: var(--bs-modal-bg);
        @include opacity (1);
        cursor: $pointer;
        position: $absolute;
        right: 15px;
        top: 10px;
        padding: 0;
        width: 20px;
        height: $auto;
        z-index: 111;
        @media (max-width:820px) {
            right: 10px;
            top: 7px;
        }
        img {
            width: 100%;
        }
    }
    .modal {
        &-dialog-centered {
            min-height: calc(100% - 3.5rem);
        }
        &-content {
            background: $white-color;
            @include border-radius (1.5rem);
            @media (max-width:820px) {
                @include border-radius (1rem);
            }
        }
        &-body {
            padding: 2.5rem;
            @media (max-width:820px) {
                padding: 2rem;
            }
        }
        &-header {
            padding: 1rem 2rem;
            border: 0;
        }
        &-footer {
            padding: 1rem 2rem;
        }
    }
}

.confirmation_modal {
    em {
        padding: 2px;
        width: 55px;
        height: 55px;
        margin: 0 $auto;
        @include flexCenter;
        background: var(--bs-modal-bg);
        @include border-radius (50%);
        img {
            width: 30px;
        }
    }
}