@import './../../../assets/scss/base';

.front_form {
    background: url(../../../assets/images/login_bg.webp) $no-repeat;
    background-attachment: $fixed;
    background-size: $cover;
    position: $relative;
    isolation: isolate;
    padding: 0 15px;
    @include flexbox();
    @include align-items($center);
    @include justify-content($center);
    z-index: 1;
    &::before {
        content: '';
        background: rgba($base-color, .5);
        height: 100%;
        width: 100%;
        position: $absolute;
        z-index: -1;
    }
    &.form_signup {
        .ls_form {
            @media (min-width:1200px) {
                width: 1000px;
            }
            @media (max-width:1200px) {
                width: 800px;
                max-width: 96%;
            }
        }
    }
}

.ls_form {
    margin: 1.75rem $auto;
    background: $white-color;
    @include border-radius (20px);
    @media (min-width:1200px) {
        width: 550px;
        padding: 30px 50px;
    }
    @media (max-width:1200px) {
        padding: 30px;
        width: 500px;
        max-width: 100%;
    }
    @at-root .logo_front {
        display: $inline-block;
        margin: 0 0 15px;
        img {
            width: 250px;
        }
    }
    .h6 {
        color: $base-color;
    }
    @at-root {
        .seperator {
            position: $relative;
            text-align: $center;
            isolation: isolate;
            z-index: 1;
            &::before {
                content: '';
                background: rgba($base-color, .3);
                position: $absolute;
                top: 16px;
                left: 0;
                width: 100%;
                height: 1px;
                z-index: -1;
            }
            span {
                padding: 0 7px;
                color: $base-color;
                display: $inline-block;
                @include font-size (1.2);
                background: $white-color;
                font-weight: $font-semibold;
                position: $relative;
                z-index: 11;
            }
        }

        .link_text {
            cursor: $pointer;
            @include font-size(1);
            text-decoration: $none;
            font-weight: $font-medium;
            text-transform: $capitalize;
            text-decoration: $underline;
            &:hover {
                text-decoration: $none;
            }
        }
    }
    .btn-md {
        min-width: 180px;
    }
    .form-control {
        &[type="email"],
        &[type="password"] {
            padding-right: 55px;
        }
    }
}

.auth_btns {
    gap: 10px;
}

.show_hide_pwd {
    position: $absolute;
    top: calc(50% - 30px / 2);
    right: 20px;
    background: $transparent;
    cursor: $pointer;
    border: 0;
    z-index: 1;
    img {
        width: 20px;
    }
}