@import 'assets/scss/base';
@import 'assets/scss/theme.scss';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.ver_icon {
  img {
    min-width: 18px;
  }
}
#root {
  overflow-x: $hidden;
}
// Avtar
.avtar {
  overflow: $hidden;
  position: $relative;
  @include border-radius (50%);
  border: 1px solid $grey-color-300;
  @include transition (all, .5s, ease);
  img {
    @include transition (all, .5s, ease-in-out);
    width: 100%;
    height: 100%;
    object-fit: $cover;
    position: $absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }
}

// React Country Dropdown
div.country_dropdown {
  .react-tel-input {
    padding: 0;
    border: 0;
    &:focus {
      background: $transparent;
    }
    .form-control {
      @extend .form-control;
      background: $grey-color-500;
      @include font-size (1);
      padding-left: 78px;
      width: 100%;
    }
    .flag-dropdown {
      background: $transparent;
      border: 0;
      left: 10px;
      &.open {
        background-color: $transparent;
      }
      .selected-flag {
        background-color: $transparent !important;
        &:focus {
          background-color: $transparent !important;
        }
        .arrow {
          background: url(assets/icons/down_arrow.svg) $no-repeat;
          background-size: 100%;
          width: 16px;
          height: 10px;
          border: 0;
          left: 30px;
          margin-top: -5px;
        }
      }
    }
  }
}

// Country Dropdown
.country_picker {
  // @extend .form-control;
  @include flexbox;
  padding: 0;
  select {
    width: 100%;
    line-height: 1;
    @extend .form-control;
    &[name="rcrs-country"] {
      background-color: $grey-color-500;
      @include border-radius (10px);
    }
    &[name="rcrs-region"] {
      width: 100%;
      background-color: $grey-color-500;
      @include border-radius (10px);
    }
  }
}

//Card
.custom-card {
  border-radius: 10px;
  background: #ECF5F6;
  border: 1px solid $grey-color-700;
  .card-title {
    font-weight: 700;
    padding-bottom: 10px;
  }
}