@import '../../../../assets/scss/base';

.upload_doc {
    @include border-radius (10px);
    background: $grey-color-700;
    display: $inline-block;
    position: $relative;
    min-width: 210px;
    span {
        &:not(.btn-danger) {
            width: 100%;
            padding: 11px 40px 11px 10px;
            display: $block;
            @include font-size (.90);
            height: 46px;
            line-height: 1.7;
            max-width: 200px;
            overflow: $hidden;
            text-overflow: ellipsis;
        }
    }
    .btn-danger {
        cursor: $pointer;
        position: $absolute;
        right: 0;
        top: 0;
        height: 46px;
        padding: 9px .85rem;
        background: $error-color;
        border-color: $transparent;
        @include border-radius (0 10px 10px 0);
        box-shadow: $none;
        @include flexCenter;
        svg {
            width: 16px;
            display: $inline-block;
            pointer-events: $none;
        }
    }
}

.file_upload_field {
    .upload_doc {
        position: $absolute;
        z-index: 11;
        @media (min-width:1300px) {
            top: 7px;
            left: 7px;
            width: calc(100% - 14px);
        }
        @media (max-width:1300px) {
            top: 3px;
            left: 3px;
            width: calc(100% - 6px);
        }
    }
}

.disabled {
    .data_label {
        pointer-events: $none;
        @include opacity (.4);
    }
}