@import '../../assets/scss/base';

.footer {
    border-top: 1px solid $grey-color-700;
    @media (min-width:1200px) {
        padding: 10px 0 0;
    }
    p,
    span {
        color: rgba($base-color, .7);
    }
    .footer_top {
        padding: 30px 0 15px;
    }
    .f_col {
        .h6 {
            color: $base-color;
        }
        ul {
            li {
                margin-bottom: 10px;
                a {
                    color: rgba($base-color, .7);
                    &:hover {
                        color: $base-color;
                    }
                }
            }
        }
        &.contact_menu {
            li {
                padding-left: 35px;
                position: $relative;
                min-height: 30px;
                a {
                    display: $block;
                    em {
                        img {
                            width: 24px;
                            max-width: 24px;
                        }
                    }
                    &:hover {
                        text-decoration: $underline;
                    }
                    em {
                        position: $absolute;
                        left: 0;
                        top: 0;
                    }
                }
            }
        }
    }
}

.footer_bottom {
    border-top: 1px solid $grey-color-700;
    padding: 10px 0;
}