@import '../../assets/scss/base';

.accordin_sec {
    .accordion-item {
        margin-bottom: 20px;
        .accordion-button {
            background: $base-color;
            color: $white-color;
            @include font-size (1.2);
            box-shadow: $none !important;
            &:after {
                filter: invert(1) brightness(10);
            }
        }
    }
    .accordion-body {
        background: $grey-color-500;
        @include border-radius (0 0 5px 5px);
    }
}