@import '../../../../../assets/scss/base';

.register_success {
    em {
        background: rgba($secondary-color, .5);
        @include border-radius (50%);
        padding: 10px;
        height: 60px;
        width: 60px;
        margin: 0 $auto;
        @include flexCenter;
        @media (min-width:1280px) {
            height: 80px;
            width: 80px;
        }
        img {
            width: 30px;
        }
    }
}