@import '../../assets/scss/base';

.loader_main {
    background: rgba($white-color, .5);
    @include flexCenter;
    position: $fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 1111;
}
.loader {
    width: 50px;
    height: 50px;
    border: 3px dotted $primary-color;
    border-style: solid solid dotted dotted;
    border-radius: 50%;
    display: $inline-block;
    position: $relative;
    animation: rotation 1.5s linear infinite;
    &::after {
        content: '';
        box-sizing: border-box;
        position: $absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: $auto;
        border: 3px dotted $secondary-color;
        border-style: solid solid dotted;
        width: 34px;
        height: 34px;
        @include border-radius (50%);
        animation: rotationBack 2s linear infinite;
        @include transform-origin ($center $center);
    }
}
@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@keyframes rotationBack {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(-360deg);
    }
}