@import '../../assets/scss/base';


.ls_btn {
    padding: 5px .85rem;
    color: $white-color;
    text-align: $center;
    @include border-radius(8px);
    border: 1px solid $secondary-color;
    a {
        display: $inline-block;
        color: $white-color !important;
        &:hover {
            color: $secondary-color;
            background: $transparent;
            text-decoration: $underline !important;
        }
    }
}
.userProfile {
    cursor: $pointer;
}
.avtar_info {
    cursor: $pointer;
    color: $white-color;
    span {
        text-transform: $capitalize;
    }
}

.header {
    left: 0;
    top: 0;
    width: 100%;
    padding: 10px 0;
    background: $dark-green-color-900;
    z-index: 111;
    .logo {
        width: 170px;
        @media (max-width:420px) {
            width: 140px;
        }
    }
    .navbar {
        text-align: $center;
        @media (max-width:1200px) {
            background: $primary-color;
            height: 100%;
            display: $block;
            position: $fixed;
            top: 0;
            right: 0;
            padding: 70px 0 0;
            width: 400px;
            @include transition (all, .5s, ease-in-out);
            @include transform (translateX(110%));
            box-shadow: 0 0 10px rgba($black-color, .5);
            z-index: 1111;
            @media (max-width:575px) {
                width: 100%;
            }
        }
        ul {
            list-style: $none;
            @include flexbox();
            @media (max-width:1200px) {
                @include flex-direction (column);
                height: 100%;
                overflow-X: $hidden;
                overflow-y: $auto;
            }
            li {
                cursor: $pointer;
                padding: 5px 20px;
                color: $white-color;
                display: $inline-block;
                text-transform: $capitalize;
                @media (max-width:1200px) {
                    padding: 15px 20px;
                    @include font-size (1.1);
                    border-top: 1px solid rgba($white-color, .1);
                }
                &.active,
                &:hover {
                    color: $secondary-color;
                }
            }
        }
    }
}

.header_btns {
    position: $relative;
    z-index: 1111;
}

.navbar-toggler {
    margin-left: .85rem;
    @media (min-width:1201px) {
        display: $none;
    }
    .bar {
        height: 2px;
        width: 36px;
        display: $block;
        margin: 9px $auto;
        position: $relative;
        background-color: $white-color;
        @include border-radius (10px);
        @include transition (all, 0.4s, ease-in-out);
        .bar {
            &:nth-of-type(1) {
                animation: rotateR 1.5s cubic-bezier(.5, .2, .2, 1);
            }
            &:nth-of-type(2) {
                animation: fade 1.5s cubic-bezier(.1, .8, .1, 1);
            }
            &:nth-of-type(3) {
                animation: rotateL 1.5s cubic-bezier(.5, .2, .2, 1);
            }
        }
    }
}

.nav-open {
    overflow: $hidden;
    .navbar {
        @include transform (translateX(0));
    }
    .navbar-toggler {
        .bar {
            &:nth-of-type(1) {
                transform: translateY(15px) rotate(45deg);
            }
            &:nth-of-type(2) {
                opacity: 0;
            }
            &:nth-of-type(3) {
                transform: translateY(-8px) rotate(-45deg);
            }
        }
    }
}