@import '../../assets/scss/base';

$home_pattern: url(../../assets/images/front/pattern_team.webp);

.teamSec {
    margin: 0 0 50px;
    @media (min-width:1440px) {
        margin: 0 0 40px;
    }
    .container {
        @media (min-width:1440px) {
            max-width: 1250px;
        }
    }
    @at-root {
        .our_team_wrap {
            @include transition (all, .1s, ease-in-out);
            @include border-radius (20px);
            background-color: $base-color;
            background-image: $home_pattern;
            background-repeat: $no-repeat;
            background-size: 100%;
            padding: 40px;
            @media (max-width:1200px) {
                padding: 30px;
            }
            .title_h2 {
                p {
                    max-width: 80%;
                    display: $inline-block;
                    color: rgba($white-color, .6);
                }
            }
            .team_items {
                margin-top: 55px;
                padding: 25px;
                display: $inline-block;
                background: $white-color;
                @include border-radius (50%);
                @include transition (all, .5s, ease-in-out);
                @media (max-width:767px) {
                    margin-top: 50px;
                }
                .avtar {
                    box-shadow: 0px 1px 24px rgba(82, 82, 82, 0.41);
                }
                .avtar_summary {
                    max-height: 0;
                    overflow: $hidden;
                    position: $absolute;
                    visibility: $hidden;
                    @include opacity (0);
                    transition: 250ms ease-in;
                    @at-root {
                        .social_icons {
                            padding: 0;
                            margin: 0;
                            li {
                                margin: 10px;
                                a {
                                    display: $inline-block;
                                    @include opacity (.6);
                                    &:hover {
                                        @include opacity (1);
                                    }
                                    img {
                                        max-width: 26px;
                                        max-height: 20px;
                                    }
                                }
                            }
                        }
                    }
                }
                &:hover,
                &.active {
                    padding: 0 25px 25px;
                    @include border-radius (12px);
                    .avtar {
                        margin-top: -55px;
                        transition-delay: 0.4s;
                        @include border-radius (12px);
                        @media (max-width:767px) {
                            margin-top: -40px;
                        }
                    }
                    .avtar_summary {
                        max-height: 240px;
                        margin: 10px 0 0;
                        position: $static;
                        visibility: $visible;
                        @include opacity (1);
                    }
                }
            }
        }
    }
}