@import '../../assets/scss/base';

.progressBar {
    position: $absolute;
    bottom: 0;
    left: 15px;
    height: 10px;
    width: calc(100% - 20px);
    @include border-radius (50px);
    @media (max-width:767px) {
        left: 10px;
    }
    .progresstext {
        background-color: $secondary-color;
        padding: 5px 10px;
        @include font-size (.8);
        color: $white-color;
        top: -35px;
        right: -14px;
        text-align: $center;
        position: $relative;
        display: $inline-block;
        @include border-radius (6px);
        box-shadow: 0 0 10px rgba($base-color, .2);
        &::before {
            content: '';
            border-top: 10px solid $secondary-color;
            border-left: 10px solid $transparent;
            border-right: 10px solid $transparent;
            position: $absolute;
            bottom: -7px;
            left: calc(50% - 14px/2);
        }
    }
}