@import '../../../assets/scss/base';

.register_tabs {
    margin-block-end: 30px;
    border-bottom: 1px solid rgba($base-color, .5);
    span {
        color: rgba($base-color, .8);
        @include font-size (1.1);
        cursor: $pointer;
        padding: 0 0 10px;
        display: $inline-block;
        margin-bottom: -1px;
        border-bottom: 3px solid $transparent;
        margin-right: 30px;
        &:last-child {
            margin-right: 0;
        }
        &.active,
        &:hover {
            color: $base-color;
            border-bottom: 4px solid $base-color;
        }
    }
}

.group-form {
    .row {
        [class*="col-"] {
            &:first-child,
            &:nth-child(2) {
                @media (min-width:1280px) {
                    width: 25%;
                }
            }
        }
    }
}