@import '../../../../assets/scss/base';

.tab_content {
    padding: 30px 0;
}

.tab_content {
    .row {
        [class^="col-md"] {
            @media (min-width:1201px) {
                width: 33.33%;
            }
        }
    }
}

.profile_pic {
    @include flexbox;
    margin: 0 $auto 30px;
    max-width: 200px;
    position: $relative;
    .upload_file {
        background: $dark-green-color-900;
        @include border-radius (20px);
        @include flexCenter;
        padding: 5px;
        height: 36px;
        width: 36px;
        border: 0;
        position: $absolute;
        right: 20px;
        bottom: 20px;
        cursor: $pointer;
        img {
            width: 20px;
        }
    }
}